

import { Injectable } from '@angular/core';
import { ReplaySubject, Subject } from 'rxjs';
//import { ButtonSize } from 'tds-ui';
import { TDSSafeAny } from 'tds-ui/shared/utility';
import { ButtonSize } from "tds-ui/core/config";


@Injectable()
export class TDSRadioService {
  selected$ = new ReplaySubject<TDSSafeAny>(1);
  touched$ = new Subject<void>();
  disabled$ = new ReplaySubject<boolean>(1);
  name$ = new ReplaySubject<string>(1);
  size$ = new ReplaySubject<ButtonSize>(1);
  touch(): void {
    this.touched$.next();
  }
  select(value: TDSSafeAny): void {
    this.selected$.next(value);
  }
  setDisabled(value: boolean): void {
    this.disabled$.next(value);
  }
  setName(value: string): void {
    this.name$.next(value);
  }
  setSize(value: ButtonSize): void {
    this.size$.next(value);
  }
}
