

import { Direction, Directionality } from '@angular/cdk/bidi';
import { BooleanInput } from '@angular/cdk/coercion';
import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  forwardRef,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Optional,
  SimpleChanges,
  ViewEncapsulation
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
//import { ButtonSize } from 'tds-ui';
import { InputBoolean, OnChangeType, OnTouchedType, TDSSafeAny } from 'tds-ui/shared/utility';
import { ButtonSize } from "tds-ui/core/config";


import { TDSRadioService } from './radio.service';

export type TDSRadioButtonStyle = 'outline' | 'solid';

@Component({
  selector: 'tds-radio-group',
  exportAs: 'tdsRadioGroup',
  preserveWhitespaces: false,
  template: ` <ng-content></ng-content> `,
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [
    TDSRadioService,
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => TDSRadioGroupComponent),
      multi: true
    }
  ],
  host: {
    '[class.tds-radio-group-lg]': `size === 'lg'`,
    '[class.tds-radio-group-sm]': `size === 'sm'`,
    '[class.tds-radio-group-solid]': `buttonStyle === 'solid'`,
    '[class.tds-radio-group-rtl]': `dir === 'rtl'`
  }
})
export class TDSRadioGroupComponent implements OnInit, ControlValueAccessor, OnDestroy, OnChanges {
  static ngAcceptInputType_disabled: BooleanInput;

  private value: TDSSafeAny | null = null;
  private destroy$ = new Subject<void>();
  onChange: OnChangeType = () => {};
  onTouched: OnTouchedType = () => {};
  @Input() @InputBoolean() disabled = false;
  @Input() buttonStyle: TDSRadioButtonStyle = 'outline';
  @Input() size: ButtonSize = 'md';
  @Input() tdsName: string | null = null;

  dir: Direction = 'ltr';

  constructor(
    private cdr: ChangeDetectorRef,
    private tdsRadioService: TDSRadioService,
    private elementRef: ElementRef,
    @Optional() private directionality: Directionality
  ) {
    // TODO: move to host after View Engine deprecation
    this.elementRef.nativeElement.classList.add('tds-radio-group');
  }

  ngOnInit(): void {
    this.tdsRadioService.selected$.pipe(takeUntil(this.destroy$)).subscribe(value => {
      if (this.value !== value) {
        this.value = value;
        this.onChange(this.value);
      }
    });
    this.tdsRadioService.touched$.pipe(takeUntil(this.destroy$)).subscribe(() => {
      Promise.resolve().then(() => this.onTouched());
    });

    this.directionality.change?.pipe(takeUntil(this.destroy$)).subscribe((direction: Direction) => {
      this.dir = direction;
      this.cdr.detectChanges();
    });

    this.dir = this.directionality.value;
  }

  ngOnChanges(changes: SimpleChanges): void {
    const { disabled, tdsName,size } = changes;
    if (disabled) {
      this.tdsRadioService.setDisabled(this.disabled);
    }
    if (tdsName) {
      this.tdsRadioService.setName(this.tdsName!);
    }
    if (size) {
      this.tdsRadioService.setSize(this.size);
    }
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  writeValue(value: TDSSafeAny): void {
    this.value = value;
    this.tdsRadioService.select(value);
    this.cdr.markForCheck();
  }

  registerOnChange(fn: OnChangeType): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: OnTouchedType): void {
    this.onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
    this.tdsRadioService.setDisabled(isDisabled);
    this.cdr.markForCheck();
  }
}
